<template>
    <div class="container">
        <div class="row">
            <div class="col-md-8 offset-md-2">
                <div class="card">
                    <div class="card-body">
                        <picture-input 
                            @change="onChange" 
                            accept="image/jpeg,image/png" 
                            size="5" 
                            buttonClass="btn btn-danger"
                            >
                        </picture-input>
                        <select class="form-control my-3" v-model="category">
                            <option  selected>Select a Category</option>
                            <option :value="category.id" v-for="category in categories" :key="category.id">
                                {{ category.name }}
                            </option>
                        </select>
                        <input type="text"   placeholder="Title" class="form-control  mb-3" v-model="title">
                        <wysiwyg v-model="content" />
                        <div class="text-center mt-3">
                            <button @click="createArticle()" :disabled ="loading" class="btn btn-lg btn-success">
                                 <i class="fa fa-spinner fa-spin" v-if="loading"></i>
                                    {{ loading ? '': 'Create Article' }}
                            </button>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PictureInput from 'vue-picture-input';
    import Axios from 'axios';
    import Config from '@/config.js'

export default {
     beforeRouteEnter (to, from, next) {
        if(!localStorage.getItem('auth')){
            return next({ path:'/login'});
        }
        next();
    },
    mounted(){
        this.getArticles();
    },
    components:{
        PictureInput
    },
    data(){
        return{
        title:'',
        content:'',
        image:null,
        categories:[],
        category:'',
        loading:false

        }
    },
    methods:{
        onChange (image) {
        if (image) {
            this.image = image
            }
        },
        createArticle(){
            if(!this.title || !this.content || !this.category || !this.image){
                this.$noty.error('Please fill in all Fields');
                return;
            }
            const form = new FormData();
            form.append('file', this.image);
            form.append('upload_preset', process.env.VUE_APP_CLOUDINARY_PRESET);
            form.append('api_key',process.env.VUE_APP_CLOUDINARY_API_KEY);
            this.loading =true;
            Axios.post(process.env.VUE_APP_CLOUDINARY_URL,form)
            .then( res =>{
                Axios.post(`${Config.apiURL}/articles`, {
                    title: this.title,
                    content: this.content,
                    category_id : this.category,
                    imageUrl : res.data.secure_url
            }, {
                headers: {
                    authorization: `Bearer ${this.$root.auth.token}`
                }
            }).then(()=>{
                this.loading = false;
                this.$noty.success('Article Created Successfully ');
                this.$router.push('/');
            }).catch(() =>{
                this.loading= false;
                this.$noty.error('Oops !, Something went wrong');
            });
            }).catch(()=>{
                this.loading = false;
                this.$noty.error('Oops !, Something went wrong');
            });
        },
        getArticles(){
            const categories = localStorage.getItem('categories');
            if(categories){
                this.categories = JSON.parse(categories);
                return;
            }
            Axios.get(`${Config.apiURL}/categories`)
            .then(res=>{
                this.categories = res.data.categories;
                localStorage.setItem('categories', JSON.stringify(this.categories));
            });
        }
    }
    
}
</script>