<template>
<div class="container">
<div class="row my-5">
   <div class="col-md-10 offset-md-1">
       <div class="card" v-if="!loading">
       <div class="card-header hideimg">
           <img :src="article.imageUrl" alt=""  class="card-image-top" width="800" height="500">
       </div>
       <div class="card-body">
           <h1 class="card-title text-center my-3">{{article.title}}</h1>
           <div class="article-content" v-html="article.content"></div>
        </div> 
        <div class="comments my-4">
            <vue-disqus shortname="community-19" :identifier="article.slug" :url="url"></vue-disqus>
        </div>
   </div>
   <div class="loader "  v-if="loading">
       <div class="col-md-5 offset-md-5 my-5 offset-sm-5 ">
                <i class="fa fa-spinner fa-spin f4"></i>
        </div> 
   </div>
</div>
</div>
</div>
   
</template>
<script>
import Axios from 'axios';
import Config from '@/config.js';

export default {
    data(){
        return{
            article:{},
            loading:true,
            url: window.location.href
        }
    },

    mounted(){
        this.getArticle();
    },
    methods:{
        getArticle(){
             Axios.get(`${Config.apiURL}/article/${this.$route.params.id}`)
            .then(response =>{
                this.loading=false;
                this.article = response.data.data;
               
               
            })
        }
    }
}
</script>
<style  scoped>
.f4{
    font-size: 45px;
}
@media (max-width: 600px){
    
}
.hideimg{
    overflow: hidden;
}
</style>