<template>
    <div class="card my-5 offset-md-3">
        <div class="alert alert-danger alert-dismissible fade show" role="alert" v-if="errors.message">
                <button type="button" class="close" data-dismiss="alert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    <span class="sr-only">Close</span>
                </button>
                <div class="text-danger text-center"><strong> {{errors.message}}</strong></div>
            </div>
        <div class="card-body">
            
            <h2 class="card-title text-center mb-3 ">Login</h2>
            <div class="form-group">
                <input v-bind:class="{'is-invalid': errors.email, 'is-valid':!errors.email && submitted}" v-model="email" type="text" name=""  class="form-control" placeholder="Username" aria-describedby="helpId">
                <div class="errors" v-if="errors.email" >
                    <small id="helpId" class="text-danger h6" :key="error" v-for="error in errors.email">{{error}}</small>
                </div>
                
            </div>
            <div class="form-group">
                <input v-bind:class="{'is-invalid': errors.password, 'is-valid':!errors.name && submitted}" v-model="password" type="password" name=""  class="form-control" placeholder="Password" aria-describedby="helpId">
               <div class="errors" v-if="errors.password" >
                    <small id="helpId" class="text-danger h6" :key="error" v-for="error in errors.password">{{error}}</small>
                </div>
            </div>
            <div class="form-group">
                <button @click="logUser()"  :disabled="loading" class="btn btn-success form-control">
                     <i class="fa fa-spinner fa-spin" v-if="loading"></i>
                    {{ loading ? '': 'Sign in' }}
                </button>
            </div>
             <div class="form-group">
                <p class="text-muted">If you done have an account please <router-link to="/signup">Sign up here?</router-link>  </p>
            </div>
        
        
        </div>
    </div>
    
</template>
<script>
import Axios from 'axios';
import Config from '@/config.js';

export default {
    beforeRouteEnter (to, from, next) {
        if(localStorage.getItem('auth')){
            return next({ path:'/'});
        }
        next();
    },
    data(){
        return{
            email:'',
            password:'',
            errors:{},
            submitted:false,
            loading:false
        }
    },
    methods:{
        logUser(){
            this.loading= true;
            Axios.post(`${Config.apiURL}/auth/login`, {
                    email:this.email,
                    password:this.password
            })
            .then((Response) =>{
                this.submitted = true;
                this.loading= false;
                this.$noty.success("You have Successfully Log in !");
                const { data } = Response.data;
                localStorage.setItem('auth', JSON.stringify(data));
                this.$root.auth = data;
                this.$router.push('/');
                
            })
            .catch(({ response }) =>{
                this.$noty.error("Oops, something went wrong!");
                this.loading = false;
                this.submitted = true;
                this.errors = response.data;
               
            })
            
        }
    }
}
</script>

<style scoped>
    .card{
        width:450px;
        border-radius: 10px;
        margin-left:auto;
        margin-right: auto;
    }
    @media screen and (max-width:500px){
        .card{
        width:95%;
        margin-left:auto;
        margin-right: auto;
        border-radius: 10px;
    }
    .card-title{
        font-size: 24px;
    }
    }
</style>