<template>
    <div class="card my-5 offset-md-3" >
        <div class="card-body" >
            <h2 class="card-title text-center mb-3 ">Sign Up</h2>
            <div class="form-group">
                <input type="text" v-bind:class="{'is-invalid':errors.name, 'is-valid': !errors.name && submitted}"  v-model="username"  class="form-control" placeholder="Username" aria-describedby="helpId">
                <div class="errors" v-if="errors.name">
                        <small id="helpId" class=" text-danger h6" v-for="error in errors.name" :key="error">{{ error}}</small>
                </div>
                
            </div>
            <div class="form-group">
                
                <input type="text" v-bind:class="{'is-invalid' :errors.email, 'is-valid': !errors.email && submitted}" v-model="email"  class="form-control" placeholder="Email" aria-describedby="helpId">
                <div class="errors" v-if="errors.email">
                        <small id="helpId" class=" text-danger h6" v-for="error in errors.email" :key="error">{{ error}}</small>
                </div>
            </div>
            <div class="form-group">
                
                <input v-bind:class="{'is-invalid':errors.password, 'is-valid' : !errors.password && submitted}" type="password" v-model="password"  class="form-control" placeholder="Password" aria-describedby="helpId">
                <div class="errors" v-if="errors.password">
                        <small id="helpId" class=" text-danger h6" v-for="error in errors.password" :key="error">{{ error}}</small>
                </div>
            </div>
            <div class="form-group">
                <button @click="registerUsers()"  :disabled="loading" class="btn btn-success form-control">
                    <i class="fa fa-spinner fa-spin" v-if="loading"></i>
                        {{loading ? '': 'Sign Up'}}
                    </button>
            </div>
            <div class="form-group">
                <p class="text-muted">If you already have an account please <router-link to="/login">login here?</router-link>  </p>
            </div>
        </div>
    </div>
    
</template>
<script>
import Axios from 'axios';
import Config from '@/config.js';

export default {
    name:'signup',
    beforeRouteEnter (to, from, next) {
        if(localStorage.getItem('auth')){
            return next({ path:'/'});
        }
        next();
    },
    data(){
        return{
            username:'',
            email:'',
            password:'',
            errors: {},
            submitted: false,
            loading: false
        }
    },
    methods:{
        registerUsers(){
            this.loading= true;
            Axios.post(`${Config.apiURL}/auth/register`,{
                name:this.username,
                email:this.email,
                password:this.password
            }).then(Response =>{
                this.loading= false;
                this.submitted = true;
                const { data } = Response.data;
                this.$noty.success("You have Successfully Log in !");
                localStorage.setItem('auth', JSON.stringify(data));
                this.$root.auth = data;
                this.$router.push('/');
            }).catch(({ response } ) =>{
                this.$noty.error("Oops, something went wrong!");
                this.loading= false;
                this.submitted = true;
                this.errors = response.data;
                
            });
        }
    }
}
</script>

<style scoped>
    .card{
        width:450px;
        border-radius: 10px;
        margin-left:auto;
        margin-right: auto;
    }
    @media screen and (max-width:500px){
        .card{
        width:95%;
        margin-left:auto;
        margin-right: auto;
        border-radius: 10px;
    }
    .card-title{
        font-size: 24px;
    }
    }
</style>