<template>
    <div class="row">
        <div class="card mb-3 img">
        <div class="row no-gutters">
          <div class="col-md-4">
            <img :src="article.imageUrl" class="card-img" :alt="article.slug">
          </div>
          <div class="col-md-8">
            <div class="card-body">
              <h5 class="card-title">{{ article.title}}</h5>
              <p class="card-text" v-html="article.content.substring(0, 200)">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
              <p class="card-text"><small class="text-muted">Last updated {{new Date(article.created_at).getHours()}} mins ago</small></p>
              <p class="text-muted">Posted By: {{article.user.name}}</p>
              <a class="btn btn-primary mr-5" @click="redirectUser()" >Read more</a>
            </div>
          </div>
        </div>
     
    </div>
  </div>
</template>
<script>
export default {
  props:['article'],
  methods:{
    redirectUser(){
      if(this.$root.auth.user){
          this.$noty.success("You can view and makes Comments !");
                this.$router.push(`/article/${this.article.slug}`);
                
      }else{
            this.$noty.error("You have to Login before you can view the article and make comments");
            this.$router.push("/login");

      }
    }
  }
}
</script>
<style scoped>
  .card-img{
    max-width: 300px;
    margin:10% 1%;
   
  }
</style>