<template>
    <div class="container-fluid" >
         <div id="overlay" class="col-md-5 d-flex justify-content-center offset-md-2 " v-if="loading">
                <i class="fa fa-spinner fa-spin f4"></i>
        </div> 
        <div class="row" v-if="articles.data">
            <div class="col ">
                  <nav aria-label="Page navigation example ">
                <ul class="pagination justify-content-between mt-3">
                    <li class="page-item ">
                    <button class="page-link" @click="getprevPage()" href="#" tabindex="-1" :disabled="articles.prev_page_url=== null" >Previous</button>
                    </li>
                   
                    <li class="page-item">
                    <button @click="getNextPage()" :disabled="articles.next_page_url=== null" class="page-link" href="#">Next</button>
                    </li>
                </ul>
            </nav>
                    <div class="row mt-5 ml-auto mr-auto mwidth">
                        <div class="col-12 ml-0.5 mb-2  "  :key="article.id" v-for="article in articles.data">
                            <Article :article ="article"></Article>
                        </div>
                    </div>
              
            </div>
            <!-- <div class="col-md-3 my-5" style="z-index:1;">
                    <div class="card-title font-weight-bolder h4 ">Featured stories</div>
                    <div class="card-text h5 border py-3 px-md-4"  v-for="article in articles.data" :key="article.id">
                         <router-link  class=" " :to="`/article/${article.slug}` " >{{article.title}}</router-link>
                    </div>
                   
            </div> -->
              
        </div>  
           
    </div>  
    
</template>

<script>
import Axios from 'axios';
import Config from '@/config.js';
import Article from '@/components/Articles.vue';
export default {
    components:{
        Article
    },
    mounted(){
        this.getArticles();
    },
    data(){
        return{
            articles: {},
            loading:false
        }
    },
    methods:{
        getArticles(url=`${Config.apiURL}/articles`){
            this.loading = true;
            Axios.get(url)
            .then(response =>{
                this.loading=false;
                let { data } = response.data;
                this.articles = data;
                
               
            })
        },
        getNextPage(){
            this.getArticles(this.articles.next_page_url);
        },
        getprevPage(){
            this.getArticles(this.articles.prev_page_url);
        }
    }
}
</script>
<style  scoped>
.f4{
    font-size: 45px;
}
#overlay {
    position: fixed;
    opacity: .5;
}
.mwidth{
    max-width:80%;
}
</style>